import React from 'react'
import Image from 'next/image'
import { FaPlayCircle } from 'react-icons/fa'
import { CiBoxList } from 'react-icons/ci'

const Collectio = () => {

    const sliderRef = useRef(null);

  const scrollLeft = () => {
    sliderRef.current.scrollBy({
      left: -300,
      behavior: "smooth",
    });
  };

  const scrollRight = () => {
    sliderRef.current.scrollBy({
      left: 300,
      behavior: "smooth",
    });
  };
  return (
    <div className='pt-[6rem] w-full h-full'>
        <div className='px-20 max-w-2xl'>
            <h1 className='text-3xl font-bold text-white'>Gems for you</h1>
        </div>
      <div className="px-6 sm:px-10 md:px-20 w-full mt-10">
  <div className="flex gap-5">

    {[1, 2, 3, 4, 5, 6].map((_, i) => (
      <div
        key={i}
        className="relative group min-w-[250px] h-[150px] rounded-lg overflow-hidden cursor-pointer transition-transform duration-300 hover:scale-110 z-10"
      >
        {/* Image */}
        <Image
          src="/f1.jpg"
          alt="movie"
          fill
          className="object-cover"
        />

        {/* Overlay */}
        <div className="absolute inset-0 bg-gradient-to-t from-black/80 via-black/40 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-300" />

        {/* Details */}
        <div className="absolute bottom-3 left-3 right-3 opacity-0 group-hover:opacity-100 transition-all duration-300 translate-y-4 group-hover:translate-y-0">
          <h3 className="text-white font-semibold text-sm">
            Formula 1
          </h3>
          <p className="text-xs text-gray-300 line-clamp-2">
            High-speed racing drama with elite drivers.
          </p>

          {/* Buttons */}
          <div className="flex gap-2 mt-2">
            <button className="bg-white text-black p-1 rounded-full hover:bg-[#e50914] hover:text-white transition">
              <FaPlayCircle size={18} />
            </button>
            <button className="bg-gray-700 text-white p-1 rounded-full hover:bg-gray-900 transition">
              <CiBoxList size={18} />
            </button>
          </div>
        </div>
      </div>
    ))}

  </div>
</div>


    </div>
  )
}

export default Collectio